:root {
  --bgColor1: hsl(156, 64%, 91%);
  --bgColor2: hsl(156, 64%, 50%);
  --textColor: hsl(0, 10%, 18%);
}

body {
  background: var(--bgColor2);
  scroll-behavior: smooth;
}

header,
footer {
  background-color: var(--bgColor1);
  width: 100%;
  padding: 50px;
  display: flex;
  justify-content: space-between;
  z-index: 99;
}

header a {
  text-transform: uppercase;
  font-size: 20px;
  color: var(--textColor);
  transform: scale(1);
  transition: transform 1000ms;
}

header a:hover {
  transform: scale(1.5);
  transition: transform 1000ms;
}

header h1,
footer h2 {
  font-size: 40px;
  color: var(--textColor);

  text-align: center;
}

main {
  margin: 50px 0;
}

.Bookshelf {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-content: center;

  padding: 0 50px;
  max-width: 1900px;
  margin: 0 auto;
  gap: 20px;
}
.Book {
  background-color: var(--bgColor1);
  display: flex;
  flex-direction: column;
  padding: 20px;
  border-radius: 20px;
  flex-basis: 95%;
}

.Book img {
  width: 90%;
  margin: 0 auto;
  aspect-ratio: 16/9;
}

.Book h4,
.Book h5,
.Book h6 {
  color: black;
  text-align: center;
  margin-top: 10px;
}
.Book h4 {
  font-size: 25px;
}
.Book h5 {
  font-size: 20px;
}
.Book h6 {
  font-size: 15px;
}

@media screen and (min-width: 790px) {
  .Book {
    /* flex-grow: 1; */
    flex-basis: 45%;
  }
}

@media screen and (min-width: 1170px) {
  .Book {
    /* flex-grow: 1; */
    flex-basis: 30%;
  }
}
